// Define your custom colors
$primary-color: #963484;
$hover-color: #60AFFF;

// Global styles
body {
  background-color: white;
}

.banner {
  margin-left: 5%; 
  width: 90%; 
  height: 100vh;
  
  .navbar {
    overflow: hidden;
    background-color: $primary-color;
    margin-top: 4em;
    border-radius: 5px;
   
    

    .dropdown {
      display: none; // Initially hide the dropdown on mobile
      float: left;
      overflow: hidden;

      .dropbutton {
        font-size: 16px;
        border: none;
        outline: none;
        color: white;
        padding: 14px 16px;
        background-color: inherit;
        font-family: inherit;
        margin: 0;
      

        &:hover {
          background-color: $hover-color;
        }
      }

      .dropdown-content {
        display: none;
        position: absolute;
        background-color: #f9f9f9;
        min-width: 160px;
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
        z-index: 1;

        a {
          float: none;
          color: black;
          padding: 12px 16px;
          text-decoration: none;
          display: block;
          text-align: left;

          &:hover {
            background-color: $hover-color;
          }
        }
      }

      &:hover .dropdown-content {
        display: block;
      }
    }

    .search {
      padding: 0 1.5rem;
      position: relative;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin: 0 auto;
    }
      #queryDropdown,
      #SearchInput,
      button[type="submit"] {
        padding: 2px 5px;
        font-size: 0.6em;
        margin: 2px;
        

        #queryDropdown {
          min-width: 30px;
          min-height: 25px;
          max-width: 30%;
        }

        #SearchInput {
          min-width: 50%;
          max-width: 80%; // Adjust the max-width as needed
          min-height: 25px;
        }

        #submitButton {
          min-width: 44px;
        
          background-color: black;
        
         
        
      }
    }

    // Media query for larger screens (min-width: 744px)
    @media screen and (min-width: 744px) {
      .dropdown {
        display: block; // Show the dropdown on larger screens
      }

      .search {
        flex-direction: row; // Restore flex direction for larger screens
      }
    }
  }
}
